<template>
  <v-container>
    <v-layout
      row
    >
      <v-flex xs12 sm6 offset-sm3>
        <v-list>
          <v-list-tile v-for="(player, index) in players" :key="index">
            <v-list-tile-action>
              {{player.team.abbreviation}}
            </v-list-tile-action>
            <v-list-tile-content>
              {{player.first_name}} {{player.last_name}}
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  import axios from 'axios'

  export default {
    data: () => ({
      players: []
    }),
    async mounted() {
      let news = await axios.get('https://www.balldontlie.io/api/v1/players')
      this.players = news.data.data;
    },
  }
</script>

